.customers {
    background: #F8F8F8;
    padding: 11rem 0;

    @include breakpoint($tablet-bg) {
        padding: 8rem 0;
    }
}

.customers__title {
    font-size: 4rem;
    font-weight: 700;
    text-align: center;
    margin-bottom: 7rem;

    @include grad;
}

.customers__track {
    margin-bottom: 2rem;
}

.customers__inner {
    display: flex;
    // display: flex;
    // justify-content: center;
    // justify-content: space-between;

    margin: 0 auto;
    cursor: grab;
}

.customers__item {
    // display: flex;
    // flex-direction: column;
    width: 100%;
    max-width: 60rem;
    height: 33rem;
    background: $white;
    border-radius: 20px;

    padding: 2.9rem 4.2rem 2.4rem 4.6rem;

    @include breakpoint($tablet) {
        max-width: 50rem;
    }

    @include breakpoint($mobile) {
        height: 38.7rem;

        padding: 2rem 2rem 4rem 2rem;
    }

    @include breakpoint($mobile) {
        max-width: 27rem;
    }

    filter: blur(1px);
    background: linear-gradient(90deg, rgba(106, 17, 203, 0.2) 0%, rgba(37, 117, 252, 0.2) 100%);

    &.swiper-slide-active {
        filter: blur(0);
        background: $white;
    }
}

.customers__top {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 3rem;
}

.customers__author {
    display: flex;
    align-items: center;

}

.customers__photo {
    img {
        border-radius: 2rem;
    }

    margin-right: 1.4rem;
}

.customers__name {
    font-size: 1.5rem;
    color: $black;
    font-weight: 500;

    margin-bottom: 0.5rem;
}

.customers__pos {
    font-size: 1.5rem;
    color: $black;
    font-weight: 400;
}

.customers__company {
    box-shadow: 4px 4px 20px rgba($black, 0.08);

    @include breakpoint($mobile) {
        display: none;
    }
}

.customers__quote {
    display: flex;

    margin-bottom: 1.5rem;
}

.customers__quote-icon {
    margin-right: 1rem;
}

.customers__quote-text {
    font-size: 2rem;
    color: $black;
    font-weight: 700;

    @include breakpoint($mobile) {
        font-size: 1.6rem;
    }
}

.customers__text {
    font-size: 1.7rem;
    color: $black;
    font-weight: 400;

    margin-bottom: 1rem;

    @include breakpoint($mobile) {
        font-size: 1.4rem;

        margin-bottom: 2rem;
    }

}

.customers__link {
    font-size: 1.7rem;
    font-weight: 500;

    a {
        position: relative;
        @include grad;
        &:after {
            content: "";
            // width: 100%;
            width: 0;
            height: 2px;
            position: absolute;
            bottom: -2px;
            left: 0;
            z-index: 1;
            background: linear-gradient(90deg, #6A11CB 33%, #2575FC 66%);

            transition: width 0.2s linear;
        }
        &:hover {
            &:after {
                width: 100%;
            }
        }
    }

    img {
        display: none;
    }

    @include breakpoint($mobile) {
        display: flex;
        justify-content: space-between;

        img {
            display: block;
        }
    }
}

.customers__controls {
    display: flex;
    justify-content: center;
    align-items: center;

    padding-top: 2rem;
}

.customers__arrow {
    cursor: pointer;
    padding: 0 2rem;
}

.customers__bullet {
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    background: rgb(161, 160, 160);
    border: none;

    cursor: pointer;

    &:hover {
        background: rgb(139, 139, 139);
    }
}
