.join {
    position: relative;
}

.join__bg {
    width: 100%;
    height: 100%;
    background: url('../img/join/bg.png') center top no-repeat;
    // background-size: cover;
    position: absolute;
    bottom: -10rem;
    left: 0;
    z-index: -100;

}

.join__inner {
    position: relative;
    bottom: -8rem;
    display: flex;
    align-items: center;
    overflow: hidden;

    @include breakpoint($tablet) {
        flex-direction: column;
    }

    @include breakpoint($mobile) {
        align-items: flex-start;
    }
}

.join__content {
    padding-right: 6rem;

    @include breakpoint($tablet) {
        margin-bottom: 2rem;
    }

    @include breakpoint($mobile) {
        padding-right: 0;
    }
}

.join__title {
    font-size: 4rem;
    font-weight: 700;

    @include grad;

    margin-bottom: 3rem;

    @include breakpoint($mobile) {
        font-size: 2.5rem;
        margin-bottom: 1.5rem;
    }
}

.join__text {
    font-size: 2rem;
    color: $black;
    font-weight: 500;

    margin-bottom: 1.5rem;

    @include breakpoint($mobile) {
        font-size: 1.6rem;
    }
}

.join__mail {
    font-size: 3rem;
    color: $black;
    font-weight: 700;
    text-decoration: none;

    @include breakpoint($mobile) {
        font-size: 2rem;
    }
}

.join__photo {
    position: relative;
    bottom: 0;
    right: 0;
    z-index: -100;

    img {

        @include breakpoint($tablet) {
            width: 100%;
        }
    }
}