.modal {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100000;
    width: 100%;
    height: 100%;
    // background: rgba(gray, 0.3);
    backdrop-filter: blur(5px);
    background: rgba(#261911, 0.2);

    &.active {
        display: block;
    }

}

.modal__form {
    width: 50rem;
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 10000000;

    transform: translate(-50%, -50%);

    background: #F8F8F8;

    padding: 4rem 7.5rem 3.5rem;

    display: flex;
    flex-direction: column;
    border-radius: 1.5rem;

    @include breakpoint($tablet) {
        width: 40rem;
        padding: 4rem 4.5rem 3.5rem;
    }

    @include breakpoint($mobile) {
        width: 25rem;
        padding: 3rem 2rem 1.5rem;
    }
}

.modal__close {
    position: absolute;
    top: 2.2rem;
    right: 2.2rem;

    cursor: pointer;

    @include breakpoint($mobile) {
        top: 0.8rem;
        right: 0.8rem;
    }
}

.modal__title {
    font-size: 3rem;
    color: $black;
    font-weight: 900;
    text-align: center;

    span {
        @include grad;
    }

    margin-bottom: 1.5rem;

    @include breakpoint($tablet) {
        font-size: 2.7rem;
    }

    @include breakpoint($mobile) {
        font-size: 2rem;
        margin-bottom: 1rem;
    }
}

.modal__text {
    font-size: 1.4rem;
    color: $black;
    font-weight: 500;
    text-align: center;

    margin-bottom: 3.5rem;

    @include breakpoint($mobile) {
        font-size: 1.2rem;
        margin-bottom: 2rem;
    }

}

.modal__input {
    font-size: 1.3rem;
    color: $black;
    font-weight: 500;
    padding: 1.4rem 0 1.4rem 3.4rem;

    border: none;
    background: $white;
    border-radius: 5px;
    box-shadow: 0 0 20px rgba($black, 0.08);

    margin-bottom: 1.5rem;

    &::placeholder {
        color: rgba(#26191180, 0.5);
    }

    @include breakpoint($mobile) {
        font-size: 1.1rem;
        padding: 1.2rem 0 1.2rem 2.5rem;
        margin-bottom: 1.2rem;
    }
}

.modal__textarea {
    height: 10rem;
    font-size: 1.3rem;
    color: $black;
    font-weight: 500;
    resize: none;
    border: none;
    border-radius: 5px;

    &::placeholder {
        color: rgba(#26191180, 0.5);
    }

    box-shadow: 0 0 20px rgba($black, 0.08);

    padding: 1.4rem 0 0 3.4rem;

    margin-bottom: 2rem;

    @include breakpoint($mobile) {
        height: 7rem;
        font-size: 1.1rem;
        padding: 1.2rem 0 1.2rem 2.5rem;

        margin-bottom: 1rem;
    }
}

.modal__notice {
    font-size: 1.1rem;
    color: $black;
    font-weight: 400;

    margin-bottom: 2rem;

    @include breakpoint($mobile) {
        font-size: 0.9rem;
        margin-bottom: 1rem;
    }
}