.btn {
    font-size: 1.7rem;
    color: $white;
    font-weight: 500;

    text-decoration: none;

    padding: 1rem 2.3rem;
    letter-spacing: 3%;

    background: linear-gradient(90deg, #6A11CB 0%, #2575FC 100%);
    border-radius: 1rem;
    box-shadow: 0px 5px 15px rgba(0, 70, 187, 0.4);

    &--header {
        margin-right: 4.7rem;

        @include breakpoint($tablet-bg) {
            margin-right: 0;

            margin-bottom: 3rem;
        }
    }

    &--goal {
        padding: 1.1rem 3.7rem;
    }

    transition: box-shadow 0.3s ease-in-out;
    &:hover {
        box-shadow: 0px 7px 20px rgba(0, 70, 187, 0.6);
    }
}