.process {
    background: #F8F8F8;
    position: relative;
    padding: 18.3rem 0 22.6rem;

    @include breakpoint($tablet) {
        padding: 15rem 0;
    }

    @include breakpoint($mobile) {
        padding: 6rem 0;
    }
}

.process__bg {
    width: 100%;
    height: 100%;
    background: url('../img/process/bg.png') center top no-repeat;
    // background-size: cover;
    position: absolute;
    top: -2rem;
    left: 0;
    z-index: 100;
}

.process__title {
    font-size: 4rem;
    font-weight: 700;
    text-align: center;
    @include grad;

    margin-bottom: 9rem;

    @include breakpoint($mobile) {
        margin-bottom: 5rem;
    }
}

.process__item {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 12rem;

    &:last-child {
        margin-bottom: 0;
    }

    @include breakpoint($tablet-bg) {
        flex-direction: column;
    }

    @include breakpoint($mobile) {
        margin-bottom: 6rem;
    }
}

.process__img {
    margin-right: 4.5rem;

    @include breakpoint($tablet-bg) {
        margin-right: 0;
    }

    img {
        width: 100%;
    }
}

.process__content {

    &--center {
        position: relative;
        width: 100%;
        max-width: 68.5rem;

        margin: 0 auto 12rem;

        // .process__content-text {
        //     padding: 4.9rem 3.2rem;
        // }

        .process__content-top {
            float: left;
            background: $white;

        }

        .process__content-title {

            @include grad;
        }
    }
    
}

.process__content-top {
    display: inline-block;
    float: right;
    background: linear-gradient(90deg, rgba(37, 117, 252, 0.3) 42.21%, rgba(106, 17, 203, 0.3) 93.3%);
    border-radius: 2rem;
    box-shadow: 0 0 20px rgba($black, 0.08);
    padding: 1.2rem 3.2rem 4.5rem;

    position: relative;
    bottom: -3.5rem;
    right: 0;
    z-index: 1;
}

.process__content-top-inner {
    display: flex;
    align-items: center;
}

.process__content-title {
    font-size: 2.5rem;
    color: $white;
    font-weight: 700;

    margin-right: 1rem;

    @include breakpoint($mobile) {
        font-size: 1.8rem;
    }
}

.process__content-text {
    clear: both;
    position: relative;
    z-index: 10;

    font-size: 1.7rem;
    color: $black;
    font-weight: 400;

    background: $white;
    border-radius: 2rem;
    box-shadow: 0 0 20px rgba($black, 0.08);

    height: 23.3rem;
    padding: 0 3.2rem;

    p {
        position: relative;
        top: 50%;

        transform: translateY(-50%);

        @include breakpoint($mobile) {
            all: initial;
        }
    }

    @include breakpoint($mobile) {
        height: 100%;
        padding: 2rem;
        font-size: 1.6rem;
    }
}

.process__content-img {
    position: absolute;

    &--palete {
        top: 60%;
        right: -11rem;
        z-index: 1;

        transform: translateY(-50%);
    }

    &--lamp {
        top: 0;
        right: 0;
        z-index: 1;
    }

    &--plan {
        top: -1.5rem;
        right: -6rem;
        z-index: 1;
    }
}