.about {
    position: relative;
    padding: 7rem 0 20rem;

    @include breakpoint($tablet-bg) {
        padding: 5rem 0 12rem;
    }

    @include breakpoint($tablet) {
        padding: 4rem 0 8rem;
    }

    @include breakpoint($mobile) {
        padding: 4rem 0;
    }
}

.about__bg {
    width: 100%;
    height: 100%;
    background: url('../img/about/bg.png') center right no-repeat;
    // background-size: cover;
    position: absolute;
    bottom: -30rem;
    right: 0;
    z-index: 100;

    @include breakpoint($tablet-bg) {
        right: -10rem;
    }
}

.about__title {
    font-size: 4rem;
    font-weight: 700;

    @include grad;
    text-align: center;
    margin-bottom: 7rem;

    @include breakpoint($tablet) {
        margin-bottom: 5rem;
    }

    @include breakpoint($mobile) {
        font-family: $spartan;
        font-size: 2.5rem;
        margin-bottom: 4rem;
    }
}

.about__row {
    display: flex;
    justify-content: space-between;

    &--sec {
        margin-top: -5rem;

        @include breakpoint($tablet-bg) {
            margin-top: 0;
        }

        
    }

    @include breakpoint($tablet) {
        flex-direction: column;
        margin-bottom: 2rem;
    }

}

.about__item {
    width: 50%;
    padding-right: 7rem;

    @include breakpoint($tablet-bg) {
        padding-right: 5rem;
    }

    @include breakpoint($tablet) {
        padding-right: 0;
        width: 100%;
    }

    @include breakpoint($mobile) {
        order: 2;
    }
}

.about__top {
    box-shadow: 0 0 20px rgba($black, 0.08);
    border-radius: 2rem;
    margin-bottom: 4rem;
    padding: 1.2rem 3.8rem;

    font-size: 2.5rem;
    color: $black;
    font-weight: 700;
    background: $white;

    span {
        @include grad;
    }

    @include breakpoint($tablet) {
        text-align: center;
        margin-bottom: 2rem;
    }

    @include breakpoint($mobile) {
        font-size: 1.6rem;
        margin-bottom: 1.5rem;
    }
}

.about__text {
    font-size: 1.7rem;
    color: $black;
    font-weight: 400;
    
    @include breakpoint($tablet) {
        text-align: center;

        margin-bottom: 2rem;
    }

    @include breakpoint($mobile) {
        text-align: left;
        font-size: 1.6rem;
    }
}

.about__photo {
    width: 50%;
    img {
        width: 100%;
        border-radius: 2rem;
    }

    &--mr {
        margin-right: 10rem;

        @include breakpoint($tablet) {
            margin-right: 0;
            order: 2;
        }

        @include breakpoint($mobile) {
            order: 1;
        }
    }

    @include breakpoint($tablet) {
        width: 70%;
        margin: 0 auto;
    }

    @include breakpoint($mobile) {
        width: 100%;
        order: 1;
    }
}