.header {
    padding: 2rem 0;
}

.header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &.active {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 1000000;
        flex-direction: column;
        background: $white;
        justify-content: flex-start;
        width: 100%;
        height: 100vh;

        padding-top: 2rem;
        .header__logo {
            width: 100%;
            display: flex;
            justify-content: space-between;
            padding: 0 2rem;
            align-items: center;
            margin-bottom: 8rem;
        }

        .header__logo-img {

            @include breakpoint($mobile) {
                width: 17rem;
            }
        }

        .header__logo-cross {
            width: 3rem;
            display: block;
            object-fit: contain;

            img {
                width: 3rem;
            }

            @include breakpoint($mobile) {
                width: 2rem;

                img {
                    width: 2rem;
                }
            }
        }
        
    }
}

.header__logo-img {

    @include breakpoint($mobile) {
        width: 15rem;
    }
}

.header__logo-cross {
    display: none;
}

.header__right {
    display: flex;
    align-items: center;

    @include breakpoint($tablet-bg) {
        display: none;
    }

    &.active {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
}