.team {

    padding-bottom: 5rem;

    @include breakpoint($tablet-bg) {
        padding-bottom: 3rem;
    }

    @include breakpoint($tablet) {
        padding-bottom: 0;
    }
}

.team__title {
    font-size: 4rem;
    font-weight: 700;
    @include grad;
    text-align: center;

    margin-bottom: 2rem;

    @include breakpoint($mobile) {
        font-size: 2.5rem;
        margin-bottom: 1.5rem;
    }
}

.team__text {
    width: 63rem;
    font-size: 1.7rem;
    color: $black;
    font-weight: 400;
    text-align: center;

    margin: 0 auto 7rem;

    @include breakpoint($tablet) {
        width: 100%;
        margin: 0;

        margin-bottom: 5rem;
    }

    @include breakpoint($mobile) {
        font-size: 1.6rem;
    }
}

.team__inner {
    display: flex;
    flex-wrap: wrap;
}

.team__item {
    width: 20%;
    padding: 0 1rem;

    margin-bottom: 4rem;

    @include breakpoint($tablet-bg) {
        width: 25%;
    }

    @include breakpoint($tablet) {
        width: 33.3333%;
    }

    @include breakpoint($mobile) {
        width: 50%;
    }
}

.team__photo {
    text-align: center;
    img {
        border-radius: 1rem;

        @include breakpoint($tablet) {
            width: 100%;
        }
    }
}

.team__name {
    font-size: 2rem;
    color: $black;
    font-weight: 700;
    text-align: center;

    margin-bottom: 1.2rem;

    @include breakpoint($mobile) {
        font-size: 1.6rem;
    }
}

.team__pos {
    font-size: 1.5rem;
    color: $black;
    font-weight: 500;
    text-align: center;

    @include breakpoint($mobile) {
        font-size: 1.2rem;
    }
}