.form {
    width: 50%;
    display: flex;
    flex-direction: column;

    padding-right: 6.5rem;
    position: relative;
    z-index: 1000;

    @include breakpoint($tablet-bg) {
        width: 100%;
        padding-right: 0;

        margin-bottom: 5rem;
    }

    @include breakpoint($mobile) {
        margin-bottom: 11rem;
    }
}

.form__title {
    font-size: 4rem;
    font-weight: 700;

    @include grad;
    text-align: center;
    margin-bottom: 5rem;

    @include breakpoint($mobile) {
        font-size: 2.5rem;
        margin-bottom: 3rem;
    }
}

.form__text {
    font-size: 1.4rem;
    color: $black;
    font-weight: 500;
    text-align: center;
    margin-bottom: 3.5rem;
}

.form__input {
    width: 100%;
    font-size: 1.6rem;
    color: $black;
    border: none;
    border-radius: 5px;
    box-shadow: 0 0 20px rgba($black, 0.08);
    padding: 2rem 0 1.5rem 3rem;
    margin-bottom: 2rem;

    &::placeholder {
        color: rgba($black, 0.5);
    }

    &:focus {
        outline: none;
    }

    @include breakpoint($mobile) {
        font-size: 1.3rem;
    }

}

.form__textarea {
    width: 100%;
    font-family: 'Rubik', sans-serif;;
    font-size: 1.6rem;
    color: $black;
    border: none;
    border-radius: 5px;
    box-shadow: 0 0 20px rgba($black, 0.08);
    padding: 2rem 0 1.5rem 3rem;
    margin-bottom: 2rem;
    resize: none;

    &::placeholder {
        color: rgba($black, 0.5);
    }

    &:focus {
        outline: none;
    }
}

.form__notice {
    font-size: 1.5rem;
    color: $black;
    font-weight: 400;

    margin-bottom: 2rem;

    @include breakpoint($mobile) {
        font-size: 1.2rem;
    }
}

.form__btn {
    font-size: 2.5rem;
    color: $white;
    font-weight: 900;
    background: $black;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    padding: 1.2rem 0;

    // transition: background 0.3s linear;
    &:hover {
        background: linear-gradient(90deg, #6A11CB 0%, #2575FC 100%);
    }

    @include breakpoint($mobile) {
        font-size: 1.7rem;
    }
}