.product {
    background: #F8F8F8;
    padding: 6rem 0 8.5rem;

    @include breakpoint($mobile) {
        padding: 3.5rem 0 4.5rem;
    }
}

.product__inner {
    display: flex;
    flex-direction: column;
}

.product__title {
    font-size: 3rem;
    color: $black;
    font-weight: 500;

    margin-bottom: 5rem;
    text-align: center;

    span {
        @include grad;
    }

    @include breakpoint($mobile) {
        font-size: 2rem;
    }
}

.product__video {
    text-align: center;
    position: relative;
}

.product__video-main {
    width: 100%;
    max-width: 97rem;
}

.product__video-play {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 100;

    transform: translate(-50%, -50%);

    cursor: pointer;

    @include breakpoint($mobile) {
        width: 5rem;
        height: 5rem;
    }
}

.product__video-sound {
    position: absolute;
    top: 4rem;
    right: 14rem;
    z-index: 100;

    cursor: pointer;

    @include breakpoint($mobile) {
        display: none;
    }
}