.why {
    padding: 9rem 0 20rem;

    @include breakpoint($tablet-bg) {
        padding: 4rem 0 10rem;
    }
}

.whyus {
    position: relative;
    z-index: 100;
}

.whyus__bg {
    width: 100%;
    height: 100%;
    background: url('../img/why/bg1.png') center no-repeat;
    // background-size: cover;
    position: absolute;
    bottom: -20rem;
    left: 0;
    z-index: -100;
    overflow: hidden;

    @include breakpoint($tablet-bg) {
        bottom: -40rem;
    }
    
    @include breakpoint($tablet) {
        display: none;
    }

    @include breakpoint($mobile) {
        bottom: 0;
        background: url('../img/why/bg-mob.png') center no-repeat;
    }
}

.why__title {
    font-size: 4rem;
    font-weight: 800;

    @include grad;
    text-align: center;

    margin-bottom: 5rem;

    @include breakpoint($mobile) {
        font-size: 2.5rem;
    }
}

.why__row {
    display: flex;

    align-items: center;

    margin-bottom: 13rem;

    &:last-child {
        margin-bottom: 0;
    }

    @include breakpoint($tablet) {
        flex-wrap: wrap;
        margin-bottom: 0;
    }
}

.why__item {
    width: 50%;
    height: 39rem;
    box-shadow: 0 0 20px rgba($black, 0.08);
    border-radius: 2rem;

    padding: 4.3rem 3rem;

    &--photo {
        box-shadow: none;
        padding: 0;
        height: initial;
        border-radius: none;

        // margin: 0 1.5rem;

        img {
            width: 100%;
        }

        @include breakpoint($tablet) {
            order: 1;
        }
    }

    &--mr {
        @include breakpoint($tablet) {
            order: 2;
        }
    }

    &:last-child {
        margin-bottom: 0;
    }
    
    margin: 0 1.5rem;
    
    @include breakpoint($tablet) {
        width: 100%;
        height: 100%;
        margin: 0;
        margin-bottom: 5rem;
    }

}

.why__top {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 2rem;
}

.why__top-left {
    display: flex;
    align-items: center;
}

.why__number {
    font-size: 5.5rem;
    font-weight: 900;

    @include grad;
    opacity: 0.5;
    margin-right: 2rem;

    @include breakpoint($tablet-bg) {
        font-size: 5rem;
    }

    @include breakpoint($mobile) {
        font-size: 3rem;
    }
}

.why__top-title {
    width: 50%;
    font-size: 2.5rem;
    color: $black;
    font-weight: 700;

    @include breakpoint($tablet-bg) {
        font-size: 2.2rem;
    }

    @include breakpoint($mobile) {
        font-size: 1.8rem;
    }
}

.why__top-right {
    background: $grad;
    border-radius: 1rem;
    padding: 1.5rem;

    img {
        display: block;
        width: 100%;
        height: 100%;
    }

    @include breakpoint($tablet-bg) {
        display: none;
    }
}

.why__content {
    font-size: 1.7rem;
    color: $black;
    font-weight: 400;

    @include breakpoint($tablet-bg) {
        font-size: 1.5rem;
    }

    @include breakpoint($mobile) {
        font-size: 1.6rem;
    }
}

