.contact {
    background: #F8F8F8;
    padding: 11.9rem 0;
    position: relative;

    @include breakpoint($tablet-bg) {
        padding-top: 7rem;
        padding-bottom: 10rem;
    }

    @include breakpoint($tablet-bg) {
        padding-top: 5rem;
    }
}

.contact__bg {
    width: 100%;
    height: 100%;
    background: url('../img/contact/bg.png') center bottom no-repeat;
    // background-size: cover;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 100;

    @include breakpoint($tablet-bg) {
        display: none;
    }
}

.contact__inner {
    display: flex;
    justify-content: center;
    // justify-content: space-between;

    position: relative;
    z-index: 1000;

    @include breakpoint($tablet-bg) {
        flex-direction: column;
    }
}

.contact__offices {
    width: 50%;
    display: flex;
    flex-direction: column;

    padding-left: 6.5rem;

    @include breakpoint($tablet-bg) {
        width: 100%;
        padding-left: 0;
    }
}

.contact__offices-title {
    font-size: 4rem;
    color: $black;
    font-weight: 700;
    text-align: center;
    margin-bottom: 5rem;
}

.contact__tabs {
    display: flex;
    justify-content: space-between;

    margin-bottom: 3rem;

    @include breakpoint($tablet-bg) {
        justify-content: center;
    }

    @include breakpoint($mobile) {
        flex-direction: column;
        align-items: center;

        margin-bottom: 3.5rem;
    }
}

.contact__tabs-item {
    width: 27rem;
    background: $white;
    box-shadow: 0 0 20px rgba($black, 0.08);
    border-radius: 10px;
    cursor: pointer;
    padding: 3rem;
    margin-right: 3rem;

    &:last-child {
        margin-right: 0;
    }

    &.active {
        background: linear-gradient(90deg, rgba(106, 17, 203, 0.2) 0%, rgba(37, 117, 252, 0.2) 100%);
    }

    @include breakpoint($mobile) {
        margin-right: 0;
        margin-bottom: 2rem;

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.contact__tabs-contact {
    display: flex;
    justify-content: center;
}

.contact__tabs-item-icon {
    margin-right: 0.4rem;
}

.contact__tabs-item-text {
    font-family: $spartan;
    font-size: 1.5rem;
    color: $black;
    font-weight: 600;

    &--top {
        font-size: 2.5rem;
        font-weight: 700;

        margin-bottom: 4rem;
    }
}

.contact__tabs-address {
    font-family: $spartan;
    font-size: 2rem;
    color: $black;
    font-weight: 600;
    font-style: normal;

    text-align: center;
    margin-bottom: 4rem;
}

.contact__map {
    overflow: hidden;
    cursor: pointer;

    @include breakpoint($tablet-bg) {
        margin: 0 auto;
    }

    @include breakpoint($tablet) {

        img {
            width: 100%;
        }
    }

    @include breakpoint($mobile) {
        border-radius: 1rem;
        box-shadow: 0 0 20px rgba($black, 0.08);
    }
}