.solutions {
    padding: 15.7rem 0 5rem;
    position: relative;

    @include breakpoint($tablet-bg) {
        padding: 10rem 0;
    }

    @include breakpoint($mobile) {
        padding: 5.8rem 0;
    }
}

.solutions__bg {
    width: 100%;
    height: 100%;
    background: url('../img/solutions/bg.png') center top no-repeat;
    // background-size: cover;
    position: absolute;
    top: -3.5rem;
    left: 0;
    z-index: 100;

    @include breakpoint($tablet-bg) {
        width: 90rem;
        height: 90rem;
        left: 50%;
        top: -2rem;
        background-size: cover;
        transform: translateX(-50%);
    }

    @include breakpoint($tablet) {
        width: 70rem;
    }

    @include breakpoint($mobile) {
        width: 40rem;
        height: 30rem;
    }
}

.solutions__title {
    font-size: 4rem;
    // color: $black;
    font-weight: 700;
    // margin: 0 auto;
    text-align: center;
    margin-bottom: 3rem;

    @include grad;

    @include breakpoint($mobile) {
        font-size: 2.5rem;
    }
}

.solutions__text {
    width: 637px;
    font-size: 2rem;
    color: $black;
    font-weight: 500;
    text-align: center;

    margin: 0 auto 10rem;

    span {
        font-weight: 700;
    }

    @include breakpoint($tablet) {
        width: 100%;
    }

    @include breakpoint($mobile) {
        font-size: 1.6rem;
    }
}

.solutions__inner {
    display: flex;
    flex-direction: column;
}

.solutions__item {
    display: flex;

    margin-bottom: 20rem;

    &:last-child {
        margin-bottom: 0;
    }

    @include breakpoint($tablet-bg) {
        flex-direction: column;
        margin-bottom: 10rem;

        &:nth-child(even) {
            .solutions__item-photo {
                order: 1;
            }
    
            .solutions__item-content {
                order: 2;
            }
        }
    }
}

.solutions__item-content {
    width: 50%;

    @include breakpoint($tablet-bg) {
        width: 100%;
    }
}

.solutions__item-title {
    display: inline-block;
    font-size: 2.5rem;
    // color: $black;
    font-weight: 700;
    
    box-shadow: 0 0 20px rgba($black, 0.08);
    border-radius: 2rem;
    padding: 1.5rem 3.8rem;
    margin-bottom: 2rem;

    @include grad;

    @include breakpoint($tablet-bg) {
        display: block;
        text-align: center;
    }

    @include breakpoint($mobile) {
        font-size: 1.8rem;
    }
}

.solutions__item-text {
    font-size: 1.7rem;
    color: $black;
    font-weight: 400;
    line-height: 1.58;

    padding-left: 2rem;

    @include breakpoint($mobile) {
        font-size: 1.6rem;
        padding-left: 0;
    }
}

.solutions__item-photo {
    width: 50%;
    text-align: center;

    @include breakpoint($tablet-bg) {
        width: 100%;

        margin-bottom: 3rem;
    }

    @include breakpoint($tablet) {

        img {
            width: 100%;
        }
    }
}