.nav {
    margin-right: 4.7rem;

    @include breakpoint($tablet-bg) {
        margin-right: 0;
    }
}

.nav__inner {
    display: flex;
    align-items: center;

    &.active {
        flex-direction: column;
    }
}

.nav__item {
    position: relative;
    font-size: 1.7rem;
    color: $black;
    font-weight: 500;

    padding: 0 1.5rem;

    a {
        color: inherit;
        text-decoration: none;
    }

    &:after {
        content: "";
        position: absolute;
        top: 2.1rem;
        left: 50%;
        z-index: 1;
        width: 0;
        height: 3px;
        display: block;
        border-radius: 0.5rem;

        transform: translateX(-50%);
        background: linear-gradient(90deg, #6A11CB 0%, #2575FC 100%);

        transition: width 0.2s linear;
    }

    &:hover {
        &:after {
            width: 75%;
        }
    }

    @include breakpoint($tablet-bg) {
        font-size: 3rem;
        
        margin-bottom: 3rem;
    }

    @include breakpoint($tablet) {
        font-size: 2.5rem;
    }

    @include breakpoint($mobile) {
        font-size: 2rem;
    }
}