.faq {
    background: linear-gradient(90deg, rgba(106, 17, 203, 0.1) 0%, rgba(37, 117, 252, 0.1) 100%);

    padding: 10rem 0 12rem;

    @include breakpoint($tablet-bg) {
        padding: 8rem 0 12rem;
    }
}

.faq__title {
    font-family: $spartan;
    font-size: 4rem;
    font-weight: 800;

    @include grad;

    text-align: center;

    margin-bottom: 2.5rem;

    @include breakpoint($mobile) {
        font-size: 2.5rem;

        margin-bottom: 1.5rem;
    }
}

.faq__subtitle {
    font-family: $spartan;
    font-size: 2.5rem;
    color: $black;
    font-weight: 700;

    width: 50%;
    text-align: center;
    margin: 0 auto 6rem;

    @include breakpoint($tablet-bg) {
        width: 70%;
        font-size: 2.2rem;
    }

    @include breakpoint($mobile) {
        font-size: 1.6rem;

        margin: 0 auto 5rem;
    }
}

.faq__inner {
    padding: 0 20rem;

    @include breakpoint($tablet-bg) {
        padding: 0;
    }
}

.faq__item {
    background: $white;

    box-shadow: 0 0 20px rgba($black, 0.08);
    border-radius: 2rem;
    cursor: pointer;
    padding: 3.5rem 4rem;
    margin-bottom: 2rem;

    &:last-child {
        margin-bottom: 0;
    }

    &.active {
        .faq__answer {
            display: block;
        }

        .faq__quest-text {

            @include grad;
        }

        .faq__arrow {

            .faq__arrow-inactive {
                display: none;
            }

            .faq__arrow-active {
                display: block;
            }
        }
    }

    @include breakpoint($mobile) {
        padding: 1.4rem 1.5rem;
    }
}

.faq__quest {
    display: flex;
    align-items: center;

}

.faq__arrow {
    margin-right: 3rem;

    @include breakpoint($mobile) {
        transform: scale3d(0.6, 0.6, 0.1);
        margin-right: 1rem;
    }
}

.faq__arrow-inactive {
    display: block;
}

.faq__arrow-active {
    display: none;
}

.faq__quest-text {
    font-family: $spartan;
    font-size: 1.8rem;
    color: $black;
    font-weight: 600;

    @include breakpoint($mobile) {
        font-size: 1.3rem;
    }
}

.faq__answer {
    display: none;
    font-family: $spartan;
    color: $black;
    font-size: 1.8rem;
    font-weight: 500;

    @include breakpoint($mobile) {
        font-size: 1.3rem;
    }
}