.footer {
    background: $black;

    padding: 5rem 0 5.5rem;
}

.footer__inner {
    display: flex;
    justify-content: space-between;

    margin-bottom: 3.5rem;

    @include breakpoint($mobile) {
        flex-direction: column;
        justify-content: flex-start;
    }
}

.footer__left {
    width: 40%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include breakpoint($mobile) {
        width: 100%;
        order: 2;
    }
}

.footer__title {
    font-family: $spartan;
    font-size: 5rem;
    color: $white;
    font-weight: 700;

    @include breakpoint($tablet) {
        font-size: 4rem;
    }

    @include breakpoint($mobile) {
        font-size: 2.5rem;

        margin-bottom: 1.5rem;
    }
}

.footer__text {
    font-family: $spartan;
    font-size: 1.8rem;
    font-weight: 600;

    @include grad;
}

.footer__nav {
    text-align: right;

    @include breakpoint($mobile) {
        text-align: left;

        margin-bottom: 5rem;
    }
}

.footer__nav-item {
    font-family: $spartan;
    font-size: 1.7rem;
    color: $white;
    font-weight: 600;

    a {
        color: inherit;
        text-decoration: none;
    }

    margin-bottom: 2rem;

    &:last-child {
        margin-bottom: 0;
    }

    @include breakpoint($mobile) {
        margin-bottom: 3rem;

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.footer__bottom {
    display: flex;
    justify-content: space-between;

    @include breakpoint($tablet) {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    @include breakpoint($mobile) {
        align-items: flex-start;
    }
}

.footer__social {
    display: flex;
    align-items: center;

    @include breakpoint($tablet) {
        margin-bottom: 2rem;
    }
}

.footer__social-link {
    font-family: $spartan;
    font-size: 1.8rem;
    color: $white;
    font-weight: 600;

    a {
        color: inherit;
        text-decoration: none;
    }

    margin-right: 3rem;

    &:last-child {
        margin-right: 0;
    }

    &:first-child {
        @include breakpoint($mobile) {
            display: none;
        }
    }
}

.footer__copyright {
    display: flex;
    align-items: center;

    @include breakpoint($mobile) {
        flex-direction: column;
        align-items: flex-start;
    }
}

.footer__copyright-text {
    font-family: $spartan;
    font-size: 1.3rem;
    color: $white;
    font-weight: 600;

    margin-right: 2rem;

    @include breakpoint($mobile) {
        order: 2;
    }
}

.footer__copyright-privacy {
    font-family: $spartan;
    font-size: 1.3rem;
    color: $white;
    font-weight: 600;

    a {
        color: inherit;
    }

    @include breakpoint($mobile) {
        margin-bottom: 1.9rem;
    }
}