.langs {
    display: flex;
    cursor: pointer;
}

.langs__icon {
    margin-right: 0.7rem;
}

.langs__list {
    display: flex;
    flex-direction: column;
    margin-right: 0.5rem;

    position: relative;
}

.langs__list-item {
    display: none;

    a {
        color: inherit;
        text-decoration: none;
    }

    &:first-child {
        display: block;
    }

    &.active {
        display: block;

        &:not(:nth-child(1)) {
            position: absolute;
            top: 2rem;
            left: 0;
            z-index: 1;

            @include breakpoint($tablet-bg) {
                top: 2.5rem;
            }
        }

        &:not(:nth-child(1)):hover {
            @include grad;
        }

    }
}