.work {
    padding: 5rem 0 6.4rem;

    @include breakpoint($mobile) {
        padding: 4rem 0 6rem;
    }
}

.work__title {
    font-size: 4rem;
    font-weight: 700;
    text-align: center;

    @include grad;

    margin-bottom: 6rem;

    @include breakpoint($mobile) {
        font-size: 2.5rem;

        margin-bottom: 4.4rem;
    }
}

.work__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include breakpoint($tablet-bg) {
        flex-direction: column;
    }
}

.work__content {
    width: 50%;

    @include breakpoint($tablet-bg) {
        width: 100%;

        margin-bottom: 3rem;
    }

    @include breakpoint($mobile) {
        order: 2;
    }
}

.work__photo {
    width: 50%;

    img {
        width: 100%;
    }

    @include breakpoint($tablet-bg) {
        width: 100%;
    }

    @include breakpoint($mobile) {
        order: 1;
    }
}

.work__content-title {
    font-size: 3.5rem;
    color: $black;
    font-weight: 700;

    margin-bottom: 2rem;

    @include breakpoint($mobile) {
        font-size: 2rem;
        margin-bottom: 1rem;
    }
}

.work__text {
    font-size: 1.7rem;
    color: $black;
    font-weight: 400;

    @include breakpoint($mobile) {
        font-size: 1.6rem;
    }
}