.intro {
    padding-top: 2.5rem;
    padding-bottom: 9.5rem;

    @include breakpoint($tablet-bg) {
        padding-top: 2rem;
        padding-bottom: 5rem;
    }

    @include breakpoint($mobile) {
        padding-top: 1.5rem;
        padding-bottom: 2rem;
    }
}

.intro__inner {
    display: flex;
    align-items: center;

    @include breakpoint($tablet-bg) {
        display: block;
    }
}

.intro__content {
    width: 50%;

    @include breakpoint($tablet-bg) {
        width: 100%;
    }
}

.intro__title {
    font-size: 5.5rem;
    color: $black;
    font-weight: 700;

    margin-bottom: 3rem;

    span {
        @include grad;
    }

    @include breakpoint($tablet-bg) {
        margin-bottom: 1.5rem;
    }

    @include breakpoint($mobile) {
        font-size: 3.5rem;
    }
}

.intro__text {
    font-size: 2rem;
    color: $black;
    font-weight: 400;

    margin-bottom: 6rem;

    @include breakpoint($tablet-bg) {
        margin-bottom: 3rem;
    }

    @include breakpoint($mobile) {
        font-size: 1.6rem;
    }
}

.intro__down {
    display: flex;
    flex-direction: column;

    cursor: pointer;
    text-decoration: none;
}

.intro__down-text {
    font-size: 2rem;
    color: $black;
    font-weight: 700;

    margin-bottom: 0.7rem;

    @include breakpoint($mobile) {
        font-size: 1.6rem;
    }
}

.intro__photo {
    width: 50%;

    @include breakpoint($tablet-bg) {
        width: 100%;

        img {
            width: 100%;
        }
    }
}
