.goal {
    background: linear-gradient(90deg, rgba(106, 17, 203, 0.1) 0%, rgba(37, 117, 252, 0.1) 100%);
    padding: 6.5rem 0 11.5rem;

    position: relative;

    @include breakpoint($tablet-bg) {
        padding: 6.5rem 0 10rem;
    }
}

.goal__bg {
    width: 100%;
    height: 100%;
    background: url('../img/goal/bg.svg') center bottom no-repeat;
    // background-size: cover;
    position: absolute;
    bottom: -6.5rem;
    left: 0;
    z-index: 100;

    @include breakpoint($tablet-bg) {
        width: 95rem;
        height: 90rem;
        background-size: cover;

        bottom: -3rem;
        left: 50%;
        transform: translateX(-50%);

    }

    @include breakpoint($tablet) {
        width: 50rem;
    }
}

.goal__title {
    font-size: 5rem;
    font-weight: 700;

    @include grad;

    margin-bottom: 7rem;

    @include breakpoint($mobile) {
        font-size: 3rem;
        text-align: center;

        margin-bottom: 4rem;
    }
}

.goal__inner {
    display: flex;
    flex-direction: column;
}

.goal__item {
    display: flex;
    justify-content: space-between;
    align-items: center;

    border-top: 1px solid $black;
    padding: 4rem 0;

    &:last-child {
        border-bottom: 1px solid $black;
    }

    position: relative;
    z-index: 1000;

    @include breakpoint($tablet-bg) {
        flex-direction: column;
        padding: 4rem 7rem;
    }

    @include breakpoint($tablet) {
        padding: 3rem 4rem;
    }

    @include breakpoint($mobile) {
        padding: 3rem 1rem;

        &:first-child {
            border-top: none;
        }
    }
}

.goal__img {
    width: 15%;
    text-align: left;

    @include breakpoint($tablet-bg) {
        width: 100%;
        text-align: center;

        margin-bottom: 2rem;
    }
}

.goal__text {
    width: 70%;
    font-size: 1.7rem;
    color: $black;
    font-weight: 400;

    span {
        font-weight: 700;
    }

    @include breakpoint($tablet-bg) {
        width: 100%;

        margin-bottom: 3rem;
    }
}

.goal__btn {
    width: 15%;
    text-align: right;

    @include breakpoint($tablet-bg) {
        width: 100%;
        text-align: center;
    }

    @include breakpoint($mobile) {
        text-align: left;
    }
}